import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeView
    },
    {
      path: '/games',
      name: 'games',
      component: () => import('../views/GamesView.vue')
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('../views/AboutView.vue')
    },
    {
      path: '/records',
      name: 'records',
      component: () => import('../views/GameRecordsView.vue')
    },
    {
      path: '/achievements',
      name: 'achievements',
      component: () => import('../views/AchievementsView.vue')
    },
    // 
    {
      path: '/game/addition',
      name: 'addition-game',
      component: () => import('../views/AdditionGameView.vue')
    },
    {
      path: '/game/subtraction',
      name: 'subtraction-game',
      component: () => import('../views/SubtractionGameView.vue')
    },
    {
      path: '/game/multiplication',
      name: 'multiplication-game',
      component: () => import('../views/MultiplicationGameView.vue')
    },
    {
      path: '/game/division',
      name: 'division-game',
      component: () => import('../views/DivisionGameView.vue')
    },
    {
      path: '/game/fractions',
      name: 'fraction-game',
      component: () => import('../views/FractionGameView.vue')
    },
    {
      path: '/game/geometry',
      name: 'geometry-game',
      component: () => import('../views/GeometryGameView.vue')
    }
  ]
})

export default router
