import { achievementService } from './achievementService'

export interface GameRecord {
  id: string
  gameType: string
  gameName: string
  score: number
  level: number
  correctAnswers: number
  totalQuestions: number
  totalTime: number
  difficulty: string
  timestamp: number
  date: string
}

export interface GameStats {
  totalGames: number
  totalScore: number
  averageScore: number
  bestScore: number
  totalPlayTime: number
  averagePlayTime: number
  favoriteDifficulty: string
  lastPlayed: string
}

class GameStorageService {
  private readonly STORAGE_KEY = 'game4math_records'
  private readonly STATS_KEY = 'game4math_stats'

  // 
  getAllRecords(): GameRecord[] {
    try {
      const records = localStorage.getItem(this.STORAGE_KEY)
      return records ? JSON.parse(records) : []
    } catch (error) {
      console.error('Error reading game records:', error)
      return []
    }
  }

  // 
  getGameRecords(gameType: string): GameRecord[] {
    const allRecords = this.getAllRecords()
    return allRecords.filter(record => record.gameType === gameType)
  }

  // 
  addGameRecord(record: Omit<GameRecord, 'id' | 'timestamp' | 'date'>): void {
    try {
      const newRecord: GameRecord = {
        ...record,
        id: this.generateId(),
        timestamp: Date.now(),
        date: new Date().toLocaleDateString('zh-CN', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit'
        })
      }

      const allRecords = this.getAllRecords()
      allRecords.push(newRecord)
      
      // 100
      if (allRecords.length > 100) {
        allRecords.splice(0, allRecords.length - 100)
      }
      
      localStorage.setItem(this.STORAGE_KEY, JSON.stringify(allRecords))
      this.updateGameStats()
    } catch (error) {
      console.error('Error saving game record:', error)
    }
  }

  // 
  deleteRecord(recordId: string): void {
    try {
      const allRecords = this.getAllRecords()
      const filteredRecords = allRecords.filter(record => record.id !== recordId)
      localStorage.setItem(this.STORAGE_KEY, JSON.stringify(filteredRecords))
      this.updateGameStats()
    } catch (error) {
      console.error('Error deleting game record:', error)
    }
  }

  // 
  clearAllRecords(): void {
    try {
      localStorage.removeItem(this.STORAGE_KEY)
      localStorage.removeItem(this.STATS_KEY)
      // 
      achievementService.resetAchievements()
    } catch (error) {
      console.error('Error clearing game records:', error)
    }
  }

  // 
  getGameStats(): GameStats {
    try {
      const stats = localStorage.getItem(this.STATS_KEY)
      if (stats) {
        return JSON.parse(stats)
      }
      
      // 
      return this.calculateGameStats()
    } catch (error) {
      console.error('Error reading game stats:', error)
      return this.getDefaultStats()
    }
  }

  // 
  getGameTypeStats(gameType: string): GameStats {
    const gameRecords = this.getGameRecords(gameType)
    
    if (gameRecords.length === 0) {
      return this.getDefaultStats()
    }

    const totalGames = gameRecords.length
    const totalScore = gameRecords.reduce((sum, record) => sum + record.score, 0)
    const averageScore = Math.round(totalScore / totalGames)
    const bestScore = Math.max(...gameRecords.map(record => record.score))
    const totalPlayTime = gameRecords.reduce((sum, record) => sum + record.totalTime, 0)
    const averagePlayTime = Math.round(totalPlayTime / totalGames)
    
    // 
    const difficultyCounts: { [key: string]: number } = {}
    gameRecords.forEach(record => {
      difficultyCounts[record.difficulty] = (difficultyCounts[record.difficulty] || 0) + 1
    })
    const favoriteDifficulty = Object.entries(difficultyCounts)
      .sort(([,a], [,b]) => b - a)[0]?.[0] || 'medium'
    
    const lastPlayed = gameRecords
      .sort((a, b) => b.timestamp - a.timestamp)[0]?.date || ''

    return {
      totalGames,
      totalScore,
      averageScore,
      bestScore,
      totalPlayTime,
      averagePlayTime,
      favoriteDifficulty,
      lastPlayed
    }
  }

  // 
  private calculateGameStats(): GameStats {
    const allRecords = this.getAllRecords()
    
    if (allRecords.length === 0) {
      return this.getDefaultStats()
    }

    const totalGames = allRecords.length
    const totalScore = allRecords.reduce((sum, record) => sum + record.score, 0)
    const averageScore = Math.round(totalScore / totalGames)
    const bestScore = Math.max(...allRecords.map(record => record.score))
    const totalPlayTime = allRecords.reduce((sum, record) => sum + record.totalTime, 0)
    const averagePlayTime = Math.round(totalPlayTime / totalGames)
    
    // 
    const difficultyCounts: { [key: string]: number } = {}
    allRecords.forEach(record => {
      difficultyCounts[record.difficulty] = (difficultyCounts[record.difficulty] || 0) + 1
    })
    const favoriteDifficulty = Object.entries(difficultyCounts)
      .sort(([,a], [,b]) => b - a)[0]?.[0] || 'medium'
    
    const lastPlayed = allRecords
      .sort((a, b) => b.timestamp - a.timestamp)[0]?.date || ''

    const stats: GameStats = {
      totalGames,
      totalScore,
      averageScore,
      bestScore,
      totalPlayTime,
      averagePlayTime,
      favoriteDifficulty,
      lastPlayed
    }

    // 
    try {
      localStorage.setItem(this.STATS_KEY, JSON.stringify(stats))
    } catch (error) {
      console.error('Error saving game stats:', error)
    }

    return stats
  }

  // 
  private updateGameStats(): void {
    this.calculateGameStats()
  }

  // 
  private getDefaultStats(): GameStats {
    return {
      totalGames: 0,
      totalScore: 0,
      averageScore: 0,
      bestScore: 0,
      totalPlayTime: 0,
      averagePlayTime: 0,
      favoriteDifficulty: 'medium',
      lastPlayed: ''
    }
  }

  // ID
  private generateId(): string {
    return Date.now().toString(36) + Math.random().toString(36).substr(2)
  }

  // 
  exportGameData(): string {
    try {
      const data = {
        records: this.getAllRecords(),
        stats: this.getGameStats(),
        achievements: achievementService.getAllAchievements(),
        exportDate: new Date().toISOString(),
        version: '1.0.0'
      }
      return JSON.stringify(data, null, 2)
    } catch (error) {
      console.error('Error exporting game data:', error)
      return ''
    }
  }

  // 
  importGameData(data: string): boolean {
    try {
      const parsedData = JSON.parse(data)
      
      // 
      if (parsedData.records && Array.isArray(parsedData.records)) {
        localStorage.setItem(this.STORAGE_KEY, JSON.stringify(parsedData.records))
        this.updateGameStats()
      }
      
      // 
      if (parsedData.achievements && Array.isArray(parsedData.achievements)) {
        //  achievementService 
        achievementService.importAchievements(parsedData.achievements)
      }
      
      return true
    } catch (error) {
      console.error('Error importing game data:', error)
      return false
    }
  }
}

export const gameStorage = new GameStorageService()
